.d-flex {
  display: flex;
}
.d-flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.d-flex-center-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}
.d-flex-start-wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: row wrap;
}
.d-flex-space-between {
  display: flex;
  justify-content: space-between;
}
.d-flex-space-between-flow {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
}
.d-flex-space-around {
  display: flex;
  justify-content: space-evenly;
}

.justify-content-center {
  justify-content: center;
}
.justify-content-start {
  justify-content: start;
}
.justify-content-end {
  justify-content: end;
}
.justify-content-between {
  justify-content: space-between;
}
.justify-content-around {
  justify-content: space-around;
}
.justify-content-evenly {
  justify-content: space-evenly;
}
.justify-content-end {
  justify-content: flex-end;
}
.align-items-center {
  align-items: center;
}
.align-items-start {
  align-items: flex-start;
}
.align-items-end {
  align-items: flex-end;
}


input,
select,
textarea {
  height: 45px;
  border-radius: 5px;
  background-color: #ffffff;
  border: 1px solid rgba(112, 112, 112, 0.25);
  outline: none;
  padding: 0 12px;
  transition: all 0.5s ease-in-out;
  font-size: 14px;
  appearance: none;
  color: #4f5157;
  opacity: 0.7;
  
}

textarea {
  height: 92px;
  padding: 12px;
}
select {
  padding: 0 15px;
  margin-bottom: 20px;
  width: 100%;
  background-color: white;
  padding-left: 12px;
  padding-right: 20px;
  -webkit-appearance: none;
  background-repeat: no-repeat;
}

.modal-wrapper{
  width: 100px;
  height: 100px;
  border: solid red 2px;
}
